import React from "react";
import { Link } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

import { IProjectPreviewQueryResult } from "../../types/project.type";
import { TLang, Theme } from "../../types/common.type";

import { BsArrowUpRight } from "@react-icons/all-files/bs/BsArrowUpRight";

import { LocalBusinessMetadata } from "../common/metadata";

const ProjectPreview = ({
  project,
  className,
  theme,
  lang,
}: {
  project: IProjectPreviewQueryResult;
  className?: string;
  theme: Theme;
  lang: TLang;
}) => {
  const cover = getImage(project.frontmatter.cover);
  const { title, teamSize, duration } = project.frontmatter;

  const fontColorClass10 =
    theme === Theme.light
      ? "text-main-100 hover:text-main-10"
      : "text-secondary-100 hover:text-secondary-10";
  const bgColors =
    theme === Theme.light
      ? "bg-secondary-100 hover:bg-secondary-100"
      : "bg-main-100 hover:bg-main-100";

  return (
    <Link
      to={`/projects/${project.id}`}
      className={`${bgColors} ${fontColorClass10} flex flex-col justify-between hover:text-main-10 rounded-2xl p-2.5 gap-2.5 ${className}`}
      itemScope
      itemType="https://schema.org/CreativeWork"
    >
      <div className="group flex-1 relative">
        {cover ? (
          <GatsbyImage
            image={cover}
            alt="cover"
            itemProp="image"
            className="object-contain w-full rounded-[10px] transition-all group-hover:scale-[1.01]"
          />
        ) : null}
        <div className="absolute left-2 bottom-2 flex items-center gap-2.5 z-10">
          <p className="backdrop-blur-sm backdrop-brightness-75 text-secondary-100 px-5 h-10 flex items-center rounded-lg">
            {duration}
          </p>
          <p className="backdrop-blur-sm backdrop-brightness-75 text-secondary-100 px-5 h-10 flex items-center rounded-lg">
            {teamSize}
          </p>
        </div>
      </div>
      <div className="flex px-2.5 py-1 items-center grow">
        <h3
          className="text-base grow font-medium h-full"
          itemProp="description"
        >
          {title}
        </h3>
        <BsArrowUpRight size={20} />
      </div>
      <LocalBusinessMetadata itemProp="author" lang={lang} />
    </Link>
  );
};

export default ProjectPreview;
